import React from 'react';
import {Nav, NavItem} from 'reactstrap';
import {Link} from 'react-router-dom';

const Navb = (props) => {

    return (
        <div>
            <Nav pills className="bg-dark">
                <NavItem className="my-4 mx-auto">
                    <Link to='/News' className=''>
                        <button className="btn btn-outline-primary text-capitalize">news</button>
                    </Link>

                </NavItem>
                <NavItem className="my-auto mx-auto">
                    <Link to='/Api' className=''>
                        <button className="btn btn-outline-primary text-capitalize">statewise</button>
                    </Link>
                </NavItem>
                <NavItem className="my-auto mx-auto">
                    <Link to='/Distric' className=''>
                        <button className="btn btn-outline-primary text-capitalize">Districwise</button>
                    </Link>
                </NavItem>
                <NavItem className="my-auto mx-auto">
                    <Link to='/Reso' className=''>
                        <button className="btn btn-outline-primary text-capitalize">resources</button>
                    </Link>
                </NavItem>
            </Nav>
        </div>
    );
}

export default Navb;
